.icon-menu {
  position: relative;
  width: 24px;
  height: 24px;
  cursor: pointer;
  z-index: 5;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .icon-menu {
    display: none;
  }
}
.icon-menu span, .icon-menu::before, .icon-menu::after {
  content: "";
  transition: all 0.3s ease 0s;
  right: 0;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.icon-menu::before {
  top: 2px;
}
.icon-menu::after {
  bottom: 4px;
}
.icon-menu span {
  top: calc(50% - 2px);
}

.menu {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .menu {
    padding-right: 4px;
    height: 100%;
  }
}
@media (min-width: 1440px) {
  .menu {
    padding-right: 1px;
  }
}

@media (max-width: 768px) {
  .menu--opened .menu__body {
    display: block;
  }
  .menu--opened .icon-menu span {
    transform: scale(0);
  }
  .menu--opened .icon-menu::before {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }
  .menu--opened .icon-menu::after {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 768px) {
  .menu__body {
    position: absolute;
    display: none;
    top: 65px;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    transition-duration: 0.2s;
    /* Добавляем анимацию 0.3 сек. */
    transition-delay: 0.15s;
    /* Добавляем задержку в 0.15 сек. */
  }
}
